import React, { useState } from "react";

import RoboCard from "./robocard";

import Toast from "react-bootstrap/Toast";

import ID from "./robo-utils";

export default function RoboFeaturesGrid(props) {
	const gridData = props.configData.features;

	const [show, setShow] = useState(false);

	const emptyClick = (type, event) => {
		setShow(!show);
	};

	const popover = () => (
		<Toast
			id={ID("toast-contained")}
			onClose={() => setShow(false)}
			show={show}
			delay={3000}
			position={"top center"}
			autohide
			style={{
				marginTop: "2px",
				marginBottom: "-30px"
			}}
		>
			<Toast.Header>
				<strong className="mr-auto">
					{props.configData.labels.popup_title}
				</strong>
				<div className="badge badge-success">
					{props.configData.labels.popup_title_small}
				</div>
			</Toast.Header>
			<Toast.Body>
				<div style={{ minWidth: "200px" }}>
					{props.configData.labels.popup_text}
				</div>
			</Toast.Body>
		</Toast>
	);

	const listItems = gridData.map((el) => {
		return (
			<React.Fragment key={ID(el.type)}>
				<RoboCard
					title={el.title}
					type={el.type}
					image={el.image}
					selectGrid={props.selectGrid}
					clickCall={el.tooltip ? emptyClick : props.clickCall}
					grayscale={el.tooltip}
					small={true}
					hideTitle={true}
					imageUrl={props.imagePath}
					style={{ marginLeft: "20px", width:  "4rem" }}
				/>
			</React.Fragment>
		);
	});
	return (
		<div className="d-flex justify-content-star flex-wrap">
			{listItems}

			<div>{popover()}</div>
		</div>
	);
}
